import axios from 'axios';

const ajaxNonce = window?.fvGlobalVar.ajax_nonce;
const ajaxUrl = window?.fvGlobalVar.ajax_url;

export const axiosPost = async ({
	queryParams,
	action = 'fv_get_submissions',
}) => {
	const params = new URLSearchParams();
	params.append('action', action);
	params.append(
		'params',
		JSON.stringify({
			...queryParams,
		})
	);
	params.append('ajaxNonce', ajaxNonce);

	const res = await axios.post(`${ajaxUrl}`, params);

	return res;
};
